import {request} from '@/common/js/plugins'

export default{
	data(){
		return {
			page: 0, //
			pageNum: 6, //
			loadingType: 1, //0 1 2
			isLoading: false, //
			loaded: false, //
		}
	},
	methods: {
		/**
		 * navigatorTo
		 * @param {String} url
		 * @param {Object} options
		 * @param {Boolean} options.login   
		 */
		navTo(url, options={}){
			this.$util.throttle(()=>{
				if(!url){
					return;
				}
				if((~url.indexOf('login=1') || options.login) && !this.$store.getters.hasLogin){
					url = '/pages/auth/login';
				}
				uni.navigateTo({
					url
				})
			}, 300)
		},
		/**
		 * $http
		 * @param {String} module
		 * @param {String} operation
		 * @param {Boolean} data 
		 * @param {Boolean} ext 
		 * @param {Boolean} ext.showLoading loading
		 * @param {Boolean} ext.hideLoading loading
		 * @param {Boolean} ext.login 
		 * @param {Boolean} ext.setLoaded 
		 */
		$http(options, ext={isShowLoading: true}){
			if(ext.login && !this.$util.isLogin()){
				return Promise.reject();
			}
			return new Promise((resolve, reject)=> {
				request(options, ext).then(result => {
					resolve(result);
				}).catch((err) => {
					reject(err);
				})
			})
		},
		imageOnLoad(data, key){
			setTimeout(()=>{
				this.$set(data, 'loaded', true);
			}, 100)
		},
		showConfirm(key){
			this.$util.throttle(()=>{
				this.$refs[key].open();
			}, 200)
		},
		hidePopup(key){
			this.$refs[key].close();
		}
	}
}